import { Select } from "antd";
import { useTranslation } from "react-i18next";
import { useSearchParams } from "react-router-dom";

import classes from "./PageSizeOptions.module.scss";
import { pageSizesOptions } from "constants/general";

interface IProps {
	size?: "large" | "middle" | "small";
}

const PageSizeOptions = ({ size = "large" }: IProps) => {
	const { t } = useTranslation();
	const [search, setSearch] = useSearchParams();
	const pageSize = search.get("pageSize") ?? "10";

	return (
		<div className={classes.main}>
			<span>{t("  ")}:</span>
			<Select
				size={size}
				value={pageSize}
				style={{ width: "80px" }}
				options={pageSizesOptions}
				onSelect={(event) => {
					if (search.has("page")) search.set("page", "0");
					search.set("pageSize", String(event));
					setSearch(search);
				}}
			/>
		</div>
	);
};

export default PageSizeOptions;
