import ReactInputMask from "react-input-mask";
import classes from "./InputMask.module.scss";

interface IProps {
	mask?: string;
	value?: string;
	className?: string;
	placeholder?: string;
	alwaysShowMask?: boolean;
	maskChar?: "_" | "-" | "*" | "#" | " " | "";
	onChange?: (...args: any) => void;
}

const InputMask = (props: IProps) => {
	// const formatChars = {
	// 	"9": "[0-9]",
	// 	a: "[A-Za-z]",
	// 	"*": "[A-Za-z0-9]"
	// };
	const { value, className, placeholder, alwaysShowMask = false, mask = "\\9\\98 99 999-99-99", maskChar = "_", onChange } = props;

	return (
		<>
			{/* <Input /> */}
			<ReactInputMask
				{...props}
				mask={mask}
				value={value}
				maskChar={maskChar}
				onChange={onChange}
				placeholder={placeholder}
				alwaysShowMask={alwaysShowMask}
				className={`ant-input ${classes.input} ${className ? className : ""}`}
				// formatChars={formatChars}
				// beforeMaskedValueChange={beforeMaskedValueChange}
			/>
		</>
	);
};

export default InputMask;
