import { FC, useState } from "react";
import { useTranslation } from "react-i18next";
import { Button, Col, Input, Modal, Row, Select, Table, TableColumnsType } from "antd";
import { useModal } from "../Modals";
import DummySchema from "assets/images/png/dummy-schema.png";
import { LogoutOutlined, CheckOutlined } from "@ant-design/icons";
import { Controller, useForm } from "react-hook-form";
import { Svg } from "components/shared";
import { addSpaceEvery3Char } from "utils/number";

type TableRecordDto = any;

interface FormValues {
	name: string;
	area: string;
}

const AddComposition: FC<{ id: number }> = () => {
	const { t } = useTranslation();
	const { closeModal } = useModal();
	const [dataSource, setDataSource] = useState<FormValues[]>([]);

	const { handleSubmit, control, reset } = useForm<FormValues>({
		defaultValues: {}
	});

	const columns: TableColumnsType<TableRecordDto> = [
		{
			title: "",
			render: (_, __, idx) => idx + 1,
			align: "center",
			width: 50
		},
		{
			title: t(""),
			dataIndex: "name"
		},
		{
			title: t(""),
			dataIndex: "area",
			align: "center",
			render: (value: number) => addSpaceEvery3Char(value)
		},
		{
			title: t(""),
			align: "center",
			width: 100,
			render: () => (
				<div className="f-center">
					<Button
						type="link"
						danger
						icon={<Svg icon="trash" width={25} height={25} />}
					/>
				</div>
			)
		}
	];

	const onSubmit = handleSubmit(({ area, name }: FormValues) => {
		setDataSource((prev) => [...prev, { area, name }]);
		reset();
	});

	return (
		<Modal
			footer={null}
			open
			onCancel={closeModal}
			title={<h2 className="text-center bold mb20">{t("1-, 48 . ")}</h2>}
			width={800}
		>
			<div className="p30">
				<Row gutter={[30, 30]} className="mb40">
					<Col xs={24} lg={10}>
						<img src={DummySchema} width={225} height={234} />
					</Col>
					<Col xs={24} lg={14}>
						<form onSubmit={onSubmit}></form>
						<Row gutter={[20, 20]} className="mb20">
							<Col lg={10}>
								<Controller
									name="name"
									control={control}
									render={({ field }) => (
										<Select
											{...field}
											className="w100"
											size="large"
											placeholder={t("")}
										/>
									)}
								/>
							</Col>
							<Col lg={10}>
								<Controller
									name="area"
									control={control}
									render={({ field }) => (
										<Input
											{...field}
											className="w100"
											size="large"
											placeholder={t("")}
										/>
									)}
								/>
							</Col>
							<Col lg={3}>
								<Button
									size="large"
									icon={<LogoutOutlined />}
									onClick={onSubmit}
								/>
							</Col>
						</Row>

						<Table<TableRecordDto>
							scroll={{ y: 200 }}
							bordered
							dataSource={dataSource}
							size="small"
							columns={columns}
							pagination={false}
						/>
					</Col>
				</Row>

				<Row justify="center">
					<Button
						size="large"
						type="primary"
						shape="round"
						className="px50"
						icon={<CheckOutlined />}
					>
						{t("")}
					</Button>
				</Row>
			</div>
		</Modal>
	);
};

export default AddComposition;
