import { createSlice } from "@reduxjs/toolkit";

export interface AuthSliceState {
	user: any;
	token: string;
}

const initialState: AuthSliceState = {
	user: null,
	token: localStorage.getItem("token") || ""
};

const auth = createSlice({
	name: "auth",
	initialState: initialState,
	reducers: {
		setUser: (state: AuthSliceState, { payload }: any) => {
			state["user"] = payload;
		},
		setToken: (state: AuthSliceState, { payload }: { payload: string }) => {
			state["token"] = payload;
		},
		setLogout: (state: AuthSliceState) => {
			state["user"] = null;
			state["token"] = "";
		}
	}
});

export const authState = auth.reducer;
export const { setToken, setLogout, setUser } = auth.actions;
