import { get } from "lodash";
import dayjs, { Dayjs } from "dayjs";
import { useState, useEffect, ComponentType } from "react";
import packageJson from "../../../package.json";

const buildDateGreaterThan = (latestDate: Dayjs, currentDate?: Dayjs | number) => {
	const momLatestDateTime = dayjs(latestDate);
	const momCurrentDateTime = dayjs(currentDate);

	if (momLatestDateTime.isAfter(momCurrentDateTime)) return true;
	else return false;
};

function withClearCache(WrappedComponent: ComponentType) {
	function ClearCacheComponent(props: any) {
		const [isLatestBuildDate, setIsLatestBuildDate] = useState(false);

		useEffect(() => {
			fetch("/meta.json")
				.then((response) => response.json())
				.then((meta) => {
					const latestVersionDate = meta.buildDate;
					const currentVersionDate = get(packageJson, "buildDate");

					const shouldForceRefresh = buildDateGreaterThan(latestVersionDate, currentVersionDate);
					if (shouldForceRefresh) {
						setIsLatestBuildDate(false);
						refreshCacheAndReload();
					} else {
						setIsLatestBuildDate(true);
					}
				});
		}, []);

		const refreshCacheAndReload = () => {
			if (caches) {
				// Service worker cache should be cleared with caches.delete()
				caches.keys().then((names) => {
					for (const name of names) {
						caches.delete(name);
					}
				});
			}
			// delete browser cache and hard reload
			window.location.reload();
		};

		return <>{isLatestBuildDate ? <WrappedComponent {...props} /> : null}</>;
	}

	return ClearCacheComponent;
}

export default withClearCache;
