import cn from "classnames";
import { FC, ReactNode } from "react";
import { Table as AntTable, Skeleton, TableProps } from "antd";

import classes from "./Table.module.scss";
import { PageSizeOptions } from "./components";
import { Scroller } from "components/shared/AntComponents";

type Props = TableProps<any> & {
	width?: number | "auto";
	height?: number | "auto";
	isLoading?: boolean | ReactNode;
	isScroller?: boolean;
	scrollable?: boolean;
	mainFilter?: ReactNode | boolean;
	hasPageSize?: boolean;
	wrapClassName?: string;
	withHeadColors?: boolean;
	hasHeaderFilter?: boolean;
	headerLeftFilter?: ReactNode;
	headerRightFilter?: ReactNode;
};

const Table: FC<Props> = ({
	width = 2000,
	height = 1000,
	isLoading = false,
	mainFilter = false,
	scrollable,
	isScroller,
	hasPageSize = false,
	wrapClassName,
	withHeadColors = false,
	hasHeaderFilter = false,
	headerLeftFilter,
	headerRightFilter,
	...props
}) => {
	const classnames = cn(wrapClassName, classes.table, scrollable && classes.scrollTable, height === "auto" && classes.tableYnotScroll);

	return (
		<div className={classnames}>
			{(!!headerLeftFilter || !!headerRightFilter || hasPageSize) && (
				<div className={classes.headerFilter}>
					{headerLeftFilter && <div className={classes.left}>{headerLeftFilter}</div>}
					{headerRightFilter && (
						<div className={classes.right}>
							{headerRightFilter}
							{hasPageSize && <PageSizeOptions />}
						</div>
					)}
				</div>
			)}

			{mainFilter && <>{mainFilter}</>}

			{isLoading ? (
				Array(10)
					.fill(1)
					.map((_, key) => <Skeleton.Input key={key} active size={"large"} block style={{ marginBottom: 10 }} />)
			) : (
				<AntTable rowKey="key" id={"product_table"} pagination={false} scroll={scrollable ? { x: width, y: height } : undefined} {...props} />
			)}

			{isScroller && props.dataSource?.length !== 0 ? <Scroller tableId={"product_table"} /> : null}
		</div>
	);
};

export default Table;
