import { FC } from "react";
import cn from "classnames";
import { message } from "antd";
import { useTranslation } from "react-i18next";
import { useMutation } from "@tanstack/react-query";
import { DownloadOutlined } from "@ant-design/icons";

import Svg from "components/shared/Svg";
import AntButton from "../AntButton";
import { generateRequest } from "services/api";
import classes from "./DownloadXls.module.scss";

interface IProps {
	params?: any;
	fileName?: string;
	className?: string;
	downloadUrl?: string;
}

const DownloadXls: FC<IProps> = ({ fileName = "file", downloadUrl, params, className = "" }) => {
	const { t } = useTranslation();

	// eslint-disable-next-line
	// @ts-ignore
	const { mutate, isLoading } = useMutation(() => generateRequest().get(downloadUrl, { params, responseType: "blob" }), {
		onSuccess: (res: any) => {
			const a = document.createElement("a");
			a.href = URL.createObjectURL(new Blob([res.data], { type: `application/xlsx` }));
			a.setAttribute("download", `${fileName}.xlsx`);
			document.body.appendChild(a);
			a.click();
			document.body.removeChild(a);
		},
		onError: (err: any) => {
			message.error(err?.response?.data?.Error || "error");
		}
	});

	return (
		<div className={cn(classes.downloadXls, className)}>
			<AntButton loading={isLoading} startIcon={<DownloadOutlined />} disabled={isLoading} onClick={() => mutate()} size={"large"} className={`ml-auto pl-20 pr-20`}>
				{t("")}
			</AntButton>
		</div>
	);
};

export default DownloadXls;
