import store from "store";
import { QueryCache, QueryClient } from "@tanstack/react-query";
import axios, { AxiosError, AxiosResponse, InternalAxiosRequestConfig } from "axios";

export const authRequest = axios.create({
	baseURL: process.env.REACT_APP_AUTH
});

export const generalRequest = axios.create({
	baseURL: process.env.REACT_APP_GENERAL_API_URL
});

export const request = axios.create({
	baseURL: process.env.REACT_APP_API_URL,
	withCredentials: true
});

export function generateRequest(req?: "authRequest" | "generalRequest") {
	const _req = req === "authRequest" ? authRequest : generalRequest;

	const token = store.getState()?.auth?.token || "";

	_req.interceptors.request.use(
		(config: InternalAxiosRequestConfig) => {
			if (token) config.headers["Authorization"] = `Bearer ${token}`;
			return config;
		},
		(error: AxiosError) => Promise.reject(error)
	);

	_req.interceptors.response.use(
		(config: AxiosResponse) => config,
		(error: AxiosError) => Promise.reject(error)
	);

	return _req;
}

export const queryClient = new QueryClient({
	defaultOptions: {
		queries: {
			refetchOnWindowFocus: false
		}
	}
});

export const queryCache = new QueryCache({
	onSuccess: (data) => {
		console.log("queryCache success", data);
	},
	onError: (error) => {
		console.log("queryCache error", error);
	}
});

queryCache.clear();

export interface IResponseData<TData = any> {
	success: boolean;
	message: string;
	code: number;
	data: TData;
}
