import { FC } from "react";
import { useTranslation } from "react-i18next";
import { Pagination, PaginationProps } from "antd";

import AntButton from "../AntButton";
import Svg from "components/shared/Svg";

type Props = PaginationProps & {
	itemRender?: any;
};

const AntPagination: FC<Props> = ({ ...props }) => {
	const { t } = useTranslation();

	function itemRender(_: number, type: string, originalElement: any) {
		if (type === "prev") {
			return <AntButton startIcon={<Svg icon="arrowLeft" />}>{t("")}</AntButton>;
		}
		if (type === "next") {
			return <AntButton endIcon={<Svg icon="arrowLeft" style={{ rotate: "180deg" }} />}>{t("")}</AntButton>;
		}
		return originalElement;
	}

	return <Pagination showSizeChanger={false} itemRender={itemRender} {...props} />;
};

export default AntPagination;
