import cn from "classnames";
import { FC, ReactNode } from "react";
import { Table as AntTable, Skeleton, TableProps } from "antd";

import classes from "./ListTable.module.scss";

type TProps = TableProps<any> & {
	title?: string;
	className?: string;
	isLoading?: boolean | ReactNode;
};

const ListTable: FC<TProps> = ({ isLoading = false, className, title = "", ...props }) => {
	const classnames = cn(className, classes.table);

	return (
		<div className={classnames}>
			{title && <div className={classes.title}>{title}</div>}

			{isLoading ? (
				Array(10)
					.fill(1)
					.map((_, key) => <Skeleton.Input key={key} active size={"large"} block style={{ marginBottom: 10 }} />)
			) : (
				<AntTable rowKey="key" id={"product_table"} pagination={false} {...props} />
			)}
		</div>
	);
};

export default ListTable;
