import { CSSProperties, FC } from "react";
import { Select as AntSelect, SelectProps } from "antd";

import Label from "../Label";
import classes from "./Select.module.scss";

type Props = SelectProps<any> & {
	width?: CSSProperties["width"];
	label?: string;
};

const Select: FC<Props> = (props) => {
	const { label = "", width = "100%" } = props;
	if (label) {
		<Label label={label}>
			<AntSelect className={classes.select} {...props} style={{ width: width, ...props?.style }} />
		</Label>;
	}

	return <AntSelect className={classes.select} {...props} style={{ width: width, ...props?.style }} />;
};

export default Select;
