import VisibilitySensor from "react-visibility-sensor";

interface IProps {
	hasLoadMore: boolean;
	setPage: (...args: any) => void;
}

const LoadMoreVisible = ({ hasLoadMore = false, setPage }: IProps) => {
	if (hasLoadMore)
		return (
			<VisibilitySensor
				offset={{ bottom: -250 }}
				partialVisibility
				onChange={(isVisible: boolean) => {
					if (isVisible) setPage();
				}}>
				{() => (
					<div>
						<button onClick={setPage}></button>
					</div>
				)}
			</VisibilitySensor>
		);
	return null;
};

