import cn from "classnames";
import { get } from "lodash";
import { Dispatch, SetStateAction } from "react";

import classes from "./Tabs.module.scss";

export interface ITabs {
	id: number;
	label: string;
}

interface IProps {
	tabs: ITabs[];
	active: ITabs;
	setActive: Dispatch<SetStateAction<ITabs>>;
}

const Tabs = ({ active, setActive, tabs }: IProps) => {
	return (
		<div className={classes.tabs}>
			{tabs.map((item) => {
				return (
					<div key={item?.id} className={cn(classes.item, active?.id === item?.id && classes.active)} onClick={() => setActive(item)}>
						<span className={classes.icon} />
						<span className={classes.label}>{get(item, "label", "")}</span>
					</div>
				);
			})}
		</div>
	);
};

export default Tabs;
