import { lazy } from "react";
import { Navigate, useRoutes } from "react-router-dom";

import { Layout, NotFound } from "components";
import { ROLES } from "constants/general";
import { TRole } from "types/prop";

// Public Routes
// const Login = lazy(() => import("pages/Auth/Login"));

// Private Routes
const Contractor = lazy(() => import("pages/Contractor"));
const Architector = lazy(() => import("pages/Architector"));
const Authory = lazy(() => import("pages/Authory"));

export default function AllRoutes() {
	// const { token, user } = useUserRoles();
	// if (!user && !!token) return <PageLoader />;

	const role = (localStorage.getItem("role") ?? "CONTRACTOR") as TRole;

	const routesWithRole = (role: TRole) => {
		switch (role) {
			case ROLES.CONTRACTOR:
				return [
					{
						path: "/contractor/*",
						element: <Contractor />
					}
				];
			case ROLES.ARCHITECTOR:
				return [
					{
						path: "/architector/*",
						element: <Architector />
					}
				];
			case ROLES.AUTHORY:
				return [
					{
						path: "/authory/*",
						element: <Authory />
					}
				];
			default:
				return [];
		}
	};

	const adminRoutes = useRoutes([
		{
			path: "/",
			element: <Layout />,
			children: [
				{
					path: "/",
					index: true,
					element: (
						<Navigate
							to={
								ROLES.CONTRACTOR === role
									? "/contractor/complexes"
									: ROLES.ARCHITECTOR === role
									? "/architector/complexes"
									: ROLES.AUTHORY === role
									? "/authory"
									: "/"
							}
						/>
					)
				},
				...routesWithRole(role),
				{
					path: "*",
					element: <NotFound />
				}
			]
		}
	]);

	return adminRoutes;

	// const ghostRoutes = useRoutes([
	// 	{
	// 		path: "/",
	// 		element: <Layout />,
	// 		children: [
	// 			{
	// 				path: "",
	// 				element: <Navigate to={`/login`} />
	// 			},
	// 			{
	// 				path: "/login",
	// 				element: <Login />
	// 			},
	// 			{
	// 				path: "*",
	// 				element: <Navigate to={`/login`} />
	// 			}
	// 		]
	// 	}
	// ]);

	// if (user && token) return adminRoutes;
	// return ghostRoutes;
}
