import { FC } from "react";
import { DividerProps, Divider as AntDivider } from "antd";

const Divider: FC<DividerProps> = ({ children, ...props }) => {
	return (
		<AntDivider {...props} style={{ borderColor: "var(--sPrimary)" }}>
			{children}
		</AntDivider>
	);
};

export default Divider;
