import { ReactNode, memo, useMemo } from "react";
import { useTranslation } from "react-i18next";

import classes from "./Footer.module.scss";
import { Container } from "components/shared";
import { ReactComponent as Logo } from "assets/images/svg/logo-light.svg";
import { ReactComponent as SocialPhone } from "assets/images/svg/social-phone.svg";
import { ReactComponent as SocialTelegram } from "assets/images/svg/social-telegram.svg";
import { ReactComponent as SocialGlobe } from "assets/images/svg/social-globe.svg";

interface ISocials {
	id: number;
	href: string;
	label: string;
	icon: ReactNode;
}

const Footer = () => {
	const { t } = useTranslation();

	const socials: ISocials[] = useMemo(
		() => [
			{ id: 1, href: "tel:+998711234567", label: t("(71) 123-45-67"), icon: <SocialPhone /> },
			{ id: 2, href: "https://yangiyulshahar.uz", label: t("yangiyulshahar.uz"), icon: <SocialGlobe /> },
			{ id: 3, href: "https://t.me/yangiyulshahar", label: t("t.me/yangiyulshahar"), icon: <SocialTelegram /> }
		],
		[t]
	);

	return (
		<footer className={classes.footer}>
			<Container>
				<div className={classes.inner}>
					<div className={classes.logo}>
						<Logo />
					</div>
					<nav className={classes.socials}>
						{socials.map(({ id, href, icon, label }) => (
							<a key={id} className={classes.item} href={href}>
								<div className={classes.icon}>{icon}</div>
								<span className={classes.label}>{label}</span>
							</a>
						))}
					</nav>
				</div>
			</Container>
		</footer>
	);
};

export default memo(Footer);
